/* Custom CSS */
.py-5.bg-light {
  min-height: calc(100vh - 56px);
}

.items-section {
  position: relative;
}

.items-section + .items-section {
  margin-top: 30px;
}

.items-section-title a {
  font-size: .7em;
}

.items-section-body {
  position: relative;
  min-height: 271px;
}

.grid-item {
  display: block;
  position: relative;
  margin-bottom: 30px;
}

.grid-item-body {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  visibility: hidden;
  opacity: 0;
  -webkit-transition: all .5s ease-in-out;
  -o-transition: all .5s ease-in-out;
  transition: all .5s ease-in-out;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  background-color: rgba(0, 0, 0, .5);
  text-align: center;
}

.grid-item:hover {
  cursor: pointer;
}

.grid-item:hover .grid-item-body {
  visibility: visible;
  opacity: 1;
}

.grid-item-title {
  color: #ffffff;
  font-size: 18px;
  font-weight: bold;
}

.grid-item-date {
  color: #ffffff;
}

.detail-section {
  margin-bottom: 40px;
}

.filters-bar {
