      flex: 1 1;
  display: -ms-flexbox;
  display: flex;
  max-width: 60%;
}

.filters-bar-left .form-control {
  margin-right: 15px;
  -ms-flex-preferred-size: 33%;
      flex-basis: 33%;
}

.filters-bar-left .btn {
  margin-right: 15px;
}

.filters-bar-right {
  margin-left: auto;
}

.list-item {
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 30px;
}
.list-item-img {
  max-width: 180px;
}
.list-item-body {
  -ms-flex: 1 1;
      flex: 1 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  background-color: #eee;
